<template>
  <div class="wrapper">
    <!-- coloring button & wrappers -->
    <div class="color-show"></div>
    <div class="color-show"></div>
    <button class="bottom-right" @click="expandColor">
      <i class="fas fa-fan fa-2x"></i>
    </button>

    <!-- typography editing -->
    <button class="top-right">
      <i class="fas fa-keyboard fa-2x"></i>
    </button>
    <transition name="fade-up" mode="out-in">
      <cmenu class="menu top-right" v-if="shownMenu === 'top-right'">
        <template v-slot:h3>Typography</template>
        <template v-slot:default>
          <h4>Text Align :</h4>
          <ul>
            <li @click="changeAlign('left')">
              <i class="fas fa-align-left"></i>
            </li>
            <li @click="changeAlign('center')">
              <i class="fas fa-align-center"></i>
            </li>
            <li @click="changeAlign('right')">
              <i class="fas fa-align-right"></i>
            </li>
          </ul>
          <br />
          <h4>Font Size :</h4>
          <ul>
            <li @click="changeSize(1)"><i class="fas fa-font"></i></li>
            <li @click="changeSize(1.2)">
              <i class="fas fa-font" style="transform: scale(1.1)"></i>
            </li>
            <li @click="changeSize(1.4)">
              <i class="fas fa-font" style="transform: scale(1.2)"></i>
            </li>
          </ul>
          <br />
          <h4>Title Color :</h4>
          <input type="color" @input="changeColor('.texts h2', $event)" />
          <br />
          <h4>Paragraph Color :</h4>
          <input type="color" @input="changeColor('.texts p', $event)" />
        </template>
      </cmenu>
    </transition>

    <!--  -->
    <button class="top-left">
      <i class="fab fa-google fa-2x"></i>
    </button>
    <transition name="fade-up" mode="out-in">
      <cmenu class="menu top-left" v-if="shownMenu === 'top-left'">
        <template v-slot:h3>Google Fonts</template>
        <template v-slot:default>
          <p>
            This feature allows you to use a google font through pasting the
            google font link in the coming field :
          </p>
          <input type="url" id="glink" @input="changeFont" />
        </template>
      </cmenu>
    </transition>
    <button class="bottom-left">
      <i class="fas fa-info fa-2x"></i>
    </button>
    <transition name="fade-down" mode="out-in" appear>
      <cmenu class="menu bottom-left" v-if="shownMenu === 'bottom-left'">
        <template v-slot:h3>About this tool</template>
        <template v-slot:default>
          <p>
            This tool is a powerful text editor that has some nice features that
            will display your social media posts in a cool way.
            <br />
            this tool was developed by
            <a href="https://moneeraldakkak.github.io/Portfolio-V2/"
              >Moneer Al-Dakkak</a
            >. please feel free to contact me if you have any suggestions.
          </p>
        </template>
      </cmenu>
    </transition>

    <div class="texts">
      <h2
        contenteditable="true"
        spellcheck="false"
        @blur="insertPlaceholder($event)"
        @focus="removePlaceholder($event)"
      >
        Hi there ..
      </h2>
      <p
        contenteditable="true"
        spellcheck="false"
        @blur="insertPlaceholder($event)"
        @focus="removePlaceholder($event)"
      >
        Have fun with this wonderfol text editor !
      </p>
    </div>
  </div>
</template>

<script>
import cmenu from "./components/menu";
export default {
  name: "App",
  components: { cmenu },
  data() {
    return {
      i: 0,
      j: 0,
      a: 0,
      shownMenu: "bottom-left",
      placeholder: "<span>Type Something here</span>",
    };
  },
  methods: {
    expandColor() {
      let colors = [
        "#ffeaa7", //yellow
        "#d63031", //red
        "#3498db", //blue
        "#a29bfe", //violet
        "#27ae60", //green
        "#e84393", //pink
        "#fdcb6e", //orange
        "#c7ecee", //silver
        "#2c3e50", //dark blue
        "#00cec9", //cyan
        "#2d3436", //black
        "linear-gradient(133deg, rgba(255,52,109,1) 0%, rgba(241,140,71,1) 100%)", //po
        "linear-gradient(133deg, rgba(0,116,153,1) 0%, rgba(0,0,0,1) 100%)", //bb
        "linear-gradient(133deg, rgba(29,151,212,1) 0%, rgba(27,247,120,1) 100%)",
      ];

      this.i++;
      this.j++;

      this.j > colors.length - 1 ? (this.j = 0) : this.j;

      if (this.i % 2 === 0) {
        document.querySelectorAll(".color-show")[0].classList.add("expanded");
        document
          .querySelectorAll(".color-show")[1]
          .classList.remove("expanded");
      } else {
        document.querySelectorAll(".color-show")[1].classList.add("expanded");
        document
          .querySelectorAll(".color-show")[0]
          .classList.remove("expanded");
      }

      document.body.style.background = colors[this.j - 1];

      document.querySelector(".expanded").style.background = colors[this.j];
    },
    changeAlign(align) {
      document.querySelector(".texts").style.textAlign = align;
    },
    changeSize(size) {
      document.querySelector(".texts").style.fontSize = `${size}em`;
    },
    changeFont() {
      let link = document.getElementById("glink").value.toString();
      let fontName = link
        .replace("+", " ")
        .slice(link.indexOf("family"), link.indexOf("&"))
        .split("=")[1];

      document.querySelector(".texts").style.fontFamily = fontName;

      let linkTag = document.createElement("link");

      linkTag.setAttribute("href", link);
      linkTag.setAttribute("rel", "stylesheet");
      document.head.appendChild(linkTag);
    },
    changeColor(selector, e) {
      document.querySelector(selector).style.color = e.currentTarget.value;
    },
    insertPlaceholder(e) {
      if (e.currentTarget.textContent === "") {
        e.currentTarget.innerHTML = this.placeholder;
      }
    },
    removePlaceholder(e) {
      if (e.currentTarget.innerHTML.includes(this.placeholder)) {
        e.currentTarget.innerHTML = "";
      }
    },
  },
  mounted() {
    document.querySelectorAll("button").forEach((button) => {
      button.onclick = () => {
        if (this.shownMenu === button.getAttribute("class")) {
          this.shownMenu = "";
        } else {
          this.shownMenu = button.getAttribute("class");
        }
      };
    });
  },
};
</script>

